html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  color: #000000;
  font-family: "Roboto", sans-serif;
}

a {
  outline: unset;
  text-decoration: unset;
  color: #000000;
}

button {
  background-color: unset;
  margin: 0;
  padding: 0;
  border: unset;
  outline: unset;
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 36px;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 60px;
  }
}

h2 {
  font-size: 24px;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 48px;
  }
}

h3 {
  font-size: 16px;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 18px;
  }
}

p {
  font-size: 16px;
  line-height: 22px;
}
@media only screen and (min-width: 1000px) {
  p {
    font-size: 18px;
    line-height: 24px;
  }
}

#main_nav {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: baseline;
  gap: 10vw;
  width: 100%;
  background-color: #ffffff;
  padding: 15px;
}
@media only screen and (min-width: 900px) {
  #main_nav {
    padding: 15px 25px;
    gap: 150px;
  }
}
#main_nav .nav_link {
  color: #9d9d9d;
  padding: 5px 10px;
}
#main_nav .nav_link.name_link {
  font-size: 24px;
  line-height: 28px;
}
#main_nav .nav_content {
  position: relative;
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
#main_nav #hide-show_menu {
  position: relative;
  display: inline-flex;
  width: 32px;
  height: 19px;
  cursor: pointer;
}
#main_nav #hide-show_menu .line {
  position: absolute;
  display: block;
  background-color: #000000;
  width: 32px;
  height: 3px;
  transition: all 0.2s ease-in-out;
}
#main_nav #hide-show_menu .line:nth-child(1) {
  top: 0px;
}
#main_nav #hide-show_menu .line:nth-child(2) {
  top: 8px;
}
#main_nav #hide-show_menu .line:nth-child(3) {
  top: 16px;
}
#main_nav #hide-show_menu.active .line:nth-child(1) {
  transform: rotate(45deg);
  top: 8px;
}
#main_nav #hide-show_menu.active .line:nth-child(2) {
  height: 0;
}
#main_nav #hide-show_menu.active .line:nth-child(3) {
  transform: rotate(-45deg);
  top: 8px;
}
#main_nav #hide-show_menu.active + .nav_conent-menu.menu-toggle {
  display: flex;
}
#main_nav .nav_conent-menu {
  display: flex;
  width: 100%;
}
#main_nav .nav_conent-menu.menu-toggle {
  display: none;
  position: absolute;
  top: calc(100% + 15px);
  left: 0;
  right: 0;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 0 2px #9d9d9d;
  z-index: 9;
}
#main_nav .nav_conent-menu.menu-toggle .nav_conent-menu--user {
  margin: 0 auto;
}
#main_nav .nav_conent-menu--user {
  padding: 5px 0;
  margin: 0 0 0 auto;
}
#main_nav .nav_conent-menu--user > a {
  display: block;
  width: 40px;
  height: 40px;
  border: 1px solid #9d9d9d;
  border-radius: 20px;
  font-size: 0;
  background-image: url("../../images/ico-user.svg");
  background-size: 19px 25px;
  background-repeat: no-repeat;
  background-position: center;
}